import React from 'react';
import { HashRouter } from "react-router-dom";
import './HomeIntro.css';


export default class HomeIntro extends React.Component {
    render() {
        return (
            <HashRouter>
                <div class="intro">
                    <h1>Hello! I'm Prachi<span class="awesome">.</span> </h1>
                    <p>I am a UX designer at <a class="underline-link" href="https://www.deshawindia.com/" target="blank">D. E. Shaw</a>.</p>
                    <p>Before this, I interned at <a class="underline-link" href="https://design.google/#:~:text=Google%20Design%20is%20a%20cooperative,that%20advance%20design%20and%20technology" target="google-window"> Google</a> and studied Interaction Design at <a class="underline-link" href="http://www.idc.iitb.ac.in/" target="blank">IDC School of Design</a> at IIT Bombay. You can find my resume <a class="underline-link" href="https://drive.google.com/open?id=1ZCYzDzzSlyRpiEatI2luDXMo27-7S6qF" target="resume-window">here</a>.</p>
                    <p> Scroll down to see artefacts from my projects :)</p>
                </div>
            </HashRouter>
        );
    }
}