import React from 'react';
import './Common.css';

export default class BlogContentThree extends React.Component {
  render() {
    return (
      <div class="three">
        <div class="row justify-content-center">
          <div class="col-lg-2 col-md-2 col-sm-10 col">
            <b>{this.props.Title1}:</b> 
            <p>{this.props.Content1}</p>
          </div>
          <div class="col-lg-2 col-md-2 col-sm-10 col">
            <b>{this.props.Title2}</b>
            <p>
              {this.props.Content2}
            </p>
          </div>
          <div class="col-lg-2 col-md-2 col-sm-10 col">
            <b>{this.props.Title3}:</b> 
            <p>{this.props.Content3}</p>
          </div>
        </div>
      </div>
    );
  }
}

